body {
    background-color: black;
    font-weight: 400;
    position: relative;
    z-index: 1;
    font-family: 'Inter', sans-serif, Arial;
    color: #FFF;
}
.page-wrapper {
    max-width: 916px;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 100%;
    width: 100%;
    grid-template-rows: auto 1fr auto;
    min-height: 100vh;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 45px 0px 41px 0px;
    border-bottom: 1px solid rgba(84, 84, 84, .7);
    margin: 0 auto 40px auto;
    max-width: 880px;
    width: 100%;
}

@media screen and (max-width: 920px) {
    .header {
        padding: 28px 19px 32px 27px;
        margin-bottom: 42px;
        box-sizing: border-box;
    }
}
.header__logo {
    width: 142px;
}

@media screen and (max-width: 768px) {
    .header__logo {
        width: 104px;
    }
}
.header__link {
    color: inherit;
    text-decoration: none;
    transition: all .2s;
}

.header__link:hover {
    opacity: .85;
}
.header__link_hide {
    display: none;
}
.header__user-info {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    grid-gap: 24px;
    gap: 24px;
    line-height: 1;
}
.header__logout-button {
    background-color: transparent;
    border: none;
    outline: none;
    color: #A9A9A9;
    line-height: 1;
    cursor: pointer;
}
.profile {
    display: grid;
    max-width: 100%;
    width: 100%;
    min-width: 0;
    grid-template-columns: 120px minmax(0, 100%) 150px;
    align-items: center;
    padding: 0 19px;
    box-sizing: border-box;
}

@media screen and (max-width: 880px) {
    .profile {
        grid-template-columns: 1fr;
    }
}
.profile__add-button {
    background-color: transparent;
    border: 2px solid #fff;
    outline: none;
    display: flex;
    width: 150px;
    height: 50px;
    align-items: center;
    justify-content: center;
    transition: opacity .4s;
    cursor: pointer;
    justify-self: end;
}

.profile__add-button:hover {
    opacity: .5;
}

@media screen and (max-width: 880px) {
    .profile__add-button {
        width: 100%;
        max-width: 400px;
        margin: 0 auto;
    }
}
@media screen and (max-width: 768px) {
    .profile__add-button-icon {
        width: 16px;
    }
}
.profile__avatar {
    overflow: hidden;
    aspect-ratio: 1 / 1;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin: 0;
    position: relative;
}

@media screen and (max-width: 880px) {
    .profile__avatar {
        width: 120px;
        margin: 0 auto 26px auto;
    }
}
.profile__avatar-button {
    padding: 0;
    margin: 0;
    cursor: pointer;
    border: none;
    border-radius: 50%;
    outline: none;
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100%);
    height: calc(100%);
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    transition: all .4s;
    opacity: 0;
}

.profile__avatar-button:hover {
    opacity: 1;
}
.profile__avatar-image {
    max-height: 100%;
}
.profile__edit-button {
    background-color: transparent;
    outline: none;
    font-size: 0;
    border: 1px solid #FFF;
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: flex-end;
    width: 24px;
    aspect-ratio: 1 / 1;
    cursor: pointer;
    grid-area: profile__edit-button;
    transition: opacity .4s;
    margin-right: 30px;
    margin-bottom: 8px;
}

.profile__edit-button:hover {
    opacity: .5;
}

@media screen and (max-width: 880px) {
    .profile__edit-button {
        margin: 0 0 8px 10px;
    }
}

@media screen and (max-width: 450px) {
    .profile__edit-button {
        width: 18px;
        margin: 0 0 2px 10px;
    }
}
@media screen and (max-width: 450px) {
    .profile__edit-button-icon {
        width: 8px;
    }
}
.profile__info {
    display: grid;
    grid-template-columns: minmax(-webkit-min-content, 100%) auto;
    grid-template-columns: minmax(min-content, 100%) auto;
    min-width: 336px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    grid-template-rows: auto auto;
    grid-template-areas: "profile__title profile__edit-button"
        "profile__subtitle profile__subtitle";
    padding-left: 30px;
}

@media screen and (max-width: 880px) {
    .profile__info {
        margin: 0 auto 36px auto;
        text-align: center;
        grid-template-areas: ". profile__title profile__edit-button"
            "profile__subtitle profile__subtitle profile__subtitle";
        grid-template-columns: 18px 1fr 18px;
        max-width: calc(100% - 106px);
        position: relative;
        padding-left: 0px;
        min-width: 0;
    }
}

@media screen and (max-width: 768px) {
    .profile__info {
        max-width: calc(100% - 50px);
    }
}
.profile__subtitle {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    padding-right: 30px;
    margin: 16px 0 0 0;
    grid-area: profile__subtitle;
    max-width: 300px;
    min-width: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media screen and (max-width: 880px) {
    .profile__subtitle {
        padding-right: 0;
    }
}

@media screen and (max-width: 450px) {
    .profile__subtitle {
        padding: 0;
        margin: 14px 0 0 0;
        line-height: 1;
        font-size: 14px;
    }
}
.profile__title {
    font-weight: 500;
    font-size: 42px;
    line-height: 1.2;
    margin: 0 18px 0 0;
    grid-area: profile__title;
    max-width: 300px;
    min-width: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media screen and (max-width: 880px) {
    .profile__title {
        text-align: center;
        width: 100%;
        margin: 0;
    }
}

@media screen and (max-width: 450px) {
    .profile__title {
        font-size: 27px;
        justify-self: center;
        margin: 0;
        line-height: 1;
    }
}
.elements {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px 17px;
    gap: 20px 17px;
    padding: 50px 19px 67px 19px;
    list-style: none;
}


@media screen and (max-width: 768px) {
    .elements {
        padding: 36px 19px 48px 19px;
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 450px) {
    .elements {
        grid-template-columns: 1fr;
    }
}
.element {
    overflow: hidden;
    position: relative;
    min-width: 0;
    border-radius: 10px;
    background-color: #fff;
    font-size: 0;
    display: grid;
    grid-template-columns: 1fr -webkit-max-content;
    grid-template-columns: 1fr max-content;
    grid-template-rows: 1fr auto auto;
    grid-template-areas: "element__image element__image"
        "element__title element__like"
        "element__title element__likes-count";
}
.element__image {
    width: 100%;
    grid-area: element__image;
    aspect-ratio: 1 / 1;
    object-fit: cover;
    object-position: center center;
    cursor: pointer;
}
.element__like {
    grid-area: element__like;
    margin-right: 20px;
    align-self: flex-end;
    justify-self: center;
    cursor: pointer;
    transition: opacity .4s;
    width: 22px;
    height: 19px;
    background-image: url(/static/media/like.cb5f1643.svg);
    border: none;
    background-color: transparent;
}

.element__like:hover {
    opacity: .5;
}
.element__like_active {
    background-image: url(/static/media/like_active.eaff1f34.svg);
}
.element__likes-count {
    margin: 3px 20px 0 0;
    color: black;
    font-size: 13px;
    justify-self: center;
    grid-area: element__likes-count;
}
.element__delete-button {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 18px;
    height: 19px;
    background-color: transparent;
    border: none;
    top: 20px;
    right: 20px;
    padding: 0;
    cursor: pointer;
    transition: all .4s;
}

.element__delete-button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    opacity: .6;
}
.element__title {
    margin: 0;
    color: #000;
    font-weight: 900;
    font-size: 24px;
    line-height: 1.2;
    grid-area: element__title;
    padding: 0 0 0 20px;
    min-height: 80px;
    box-sizing: border-box;
    padding-top: 25px;
    max-width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.footer {
    display: flex;
    justify-content: flex-start;
    padding: 0 19px 60px 19px;
}

@media screen and (max-width: 768px) {
    .footer {
        padding: 0 19px 36px 19px;
    }
}
.footer__copyright {
    font-size: 18px;
    line-height: 1.2;
    margin: 0;
    color: #545454;
}


@media screen and (max-width: 650px) {
    .footer__copyright {
        font-size: 14px;
    }
}
.popup {
    display: flex;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    visibility: hidden;
    opacity: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, .5);
    z-index: 2;
    transition: all .3s;
}
.popup__container {
    box-sizing: border-box;
    position: relative;
    width: 430px;
    max-width: 100%;
    background-color: #fff;
    padding: 34px 36px 37px 36px;
    border-radius: 10px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    -moz-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    -webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}


@media screen and (max-width: 650px) {
    .popup__container {
        width: 100%;
        margin: 0 19px;
        padding: 25px 22px;
    }
}
.popup_opened {
    visibility: visible;
    opacity: 1;
}
.form {
    display: flex;
    flex-direction: column;
    padding-top: 16px;
}
.form_hidden {
    display: none;
}
.form__input {
    outline: none;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    padding: 0 0 9px 0;
    font-size: 14px;
    line-height: 1.21;
    box-sizing: border-box;
    width: 100%;
}
.form__input_type_error {
    border-bottom: 1px solid red;
}
.form__label {
    position: relative;
    margin-bottom: 30px;
}

.form__label:last-of-type {
    margin-bottom: 48px;
}

@media screen and (max-width: 650px) {
    .form__label:last-of-type {
        margin-bottom: 45px;
    }
}
.form__submit-button {
    outline: none;
    font-weight: 400;
    width: 100%;
    font-size: 18px;
    line-height: 1.2;
    text-align: center;
    color: #FFFFFF;
    background-color: #000;
    border: none;
    height: 50px;
    border-radius: 2px;
    transition: all .4s;
    cursor: pointer;
}

.form__submit-button[disabled="true"] {
    opacity: 0.2;
    border: 1px solid #000;
    color: #000;
    background-color: #fff;
    cursor: not-allowed;
}

.form__submit-button[disabled="true"]:hover {
    opacity: .2;
}

.form__submit-button:hover {
    opacity: .8;
}
@media screen and (max-width: 450px) {
    .form__submit-button {
        height: 46px;
        font-size: 14px;
    }
}
.submit-button {
