        left: auto;
        right: 0;
        bottom: calc(100% + 16px);
        width: 20px;
        height: 20px;
    }
}
.image-popup__description {
    margin: 10px 0 0 0;
    font-size: 12px;
    line-height: 1.25;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.image-popup__image {
    display: block;
    max-width: 100%;
    max-height: 80vh;
}

.info-popup__wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding: 60px 36px;
}
.info-popup__description {
    text-align: center;
    color: #000000;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 32px 0 0;
}
.error-message {
    color: red;
    display: block;
    transition: all .5s;
    opacity: 0;
    visibility: hidden;
    font-size: 12px;
    line-height: 1;
    position: absolute;
    top: calc(100% + 5px);
    left: 0;
}
.error-message_visible {
    opacity: 1;
    visibility: visible;
}
.page-title {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    margin: 0;
    padding: 20px 0 34px 0;
}
.authorization-form {
    max-width: 358px;
    margin: 0 auto;
}
.authorization-form__input {
    border: none;
    outline: none;
    width: 100%;
    padding: 0 0 13px 0;
    background-color: transparent;
    border-bottom: 2px solid #ccc;
    color: #FFF;
    margin-bottom: 30px;
}
.authorization-form__button {
    outline: none;
    border: none;
    background-color: #fff;
    width: 100%;
    padding: 14px 0;
    cursor: pointer;
    transition: all .2s;
}

.authorization-form__button:hover {
    opacity: 0.85;
}
.authorization-disclaimer {
    text-align: center;
    font-size: 14px;
    line-height: 1;
    margin: 15px 0 0 0;
}
.authorization-disclaimer__link {
    color: inherit;
    text-decoration: none;
    transition: all .2s;
}

.authorization-disclaimer__link:hover {
    opacity: 0.85;
}
 
